<template>
  <div class="header-container">
    <div class="header-content" v-if="isPc">
      <div class="header-left" @click="enterHomePage">
        <span>{{ $t('title') }}</span>
      </div>
      <div class="header-right">
        <span class="item-help" :class="{'checked-router': router.endsWith('help')}"
              @click="$router.push({path:'/support'})">{{ $t('Header.Button-1') }}</span>
        <span class="item-product" :class="{'checked-router': router.endsWith('order/place')}"
              @click="$router.push({path:'/order/place'})">{{ $t('Header.Button-2') }}</span>
        <div class="item-service" @click="enterOtherPage(`user/service`,'service')" v-if="!isLogin">
          <span :class="{'checked-router': router.endsWith('service')}">{{ $t('Header.Button-10') }}</span>
          <i :class="{'down-i': isLogin}"></i>
        </div>
        <a-popover placement="bottom" v-if="isLogin && serviceChildList.length > 0">
          <template slot="content" style="margin: 0px;">
            <p class="lang-item" :class="{'checked-lang-item': router.endsWith(item.value)}"
               v-for="(item,index) in serviceChildList" :key="index " @click.stop="goToChildPage(item)">
              {{ item.key }}
            </p>
          </template>
          <div class="item-service" @click="enterOtherPage(`/user/service`,'service')">
            <span :class="{'checked-router': router.endsWith('service')}">{{ $t('Header.Button-10') }}</span>
            <i :class="{'down-i': isLogin}"></i>
          </div>
        </a-popover>
        <a-divider style="margin:0px 32px;background: #E5E9ED;height: 16px;" type="vertical"/>
        <a-popover placement="bottom">
          <template #content>
            <p class="lang-item" :class="{'checked-lang-item': item.value === lang}"
               @click="switchLang(item)" v-for="(item,index) in langList" :key="index">{{ item.key }}</p>
          </template>
          <div class="item-service">
            <span>{{ lang === 'zh' ? '简体中文' : 'English' }}</span>
            <i class="down-i"></i>
          </div>
        </a-popover>
      </div>
    </div>
    <div class="header-content-mobile" v-if="!isPc">
      <div class="header-menu">
        <a :style="{'opacity': router.includes('/order/pay')? 0: 1}"
           :class="{'a-back': (router.includes('order/') || router.includes('/user/invoice')),'a-logo': !(router.includes(' order/') || router.includes('user/invoice'))}"
           @click="enterHomePage()"></a>
        <button class="hamburger hamburger--squeeze" type="button" @click="openMenu"
                :class="{'is-active': isOpen}"
                v-if="!(router.includes('order/ ') || router.includes('/user/invoice'))">
                    <span class="hamburger-box">
                      <span class="hamburger-inner"></span>
                    </span>
        </button>
      </div>
      <div class="menu-content" id="menu-content" :style="{display: isOpen ? 'flex': 'none'}">
        <a class="header-link" @click="enterOtherPage('/support','support')">{{ $t('Header.Button-1') }}</a>
        <a-button class="subscribe-btn" type="primary" ghost @click="enterOtherPage('order/place','product')">
          {{ $t('Header.Button-2') }}
        </a-button>
        <a class="header-link" :class="'a-top-line'"
           @click="enterOtherPage(`/user/service`,'service')">{{ $t('Header.Button-10') }}</a>
        <div v-if="isLogin">
          <a class="header-link" v-for="(item,index) in serviceChildList" :key="index"
             @click.stop="goToChildPage(item)">{{ item.key }}</a>
        </div>
        <a-radio-group class="lang-switch" v-model="langMb">
          <a-radio :value="item.value === lang ? 1 : 0" v-on:click="switchLang(item)"
                   v-for="(item,index) in langList" :key="index">{{ item.key }}
          </a-radio>
        </a-radio-group>
      </div>
    </div>
  </div>
</template>

<script>
import {AU_UUID, AU_HMAC, AU_SELECTED_SERVICE} from '@/store/Global';
import {TweenMax, Power0} from 'gsap';

export default {
  name: "Header",
  data() {
    return {
      serviceChildList: [
        {
          key: this.$t("Header.Button-11"),
          path: '/user/account',
          name: 'manageAccount'
        }, {
          key: this.$t("Header.Button-12"),
          path: 'invoice',
          name: 'manageBill'
        },
        {key: this.$t("Header.Button-13"), path: 'logout'}
      ],
      langList: [{key: '简体中文', value: 'zh'}, {key: 'English', value: 'en'}],
      lang: this.$i18n.locale,
      router: this.$route.path,
      isLogin: localStorage.getItem('AU_UUID'),
      selectedService: JSON.parse(localStorage.getItem('AU_SELECTED_SERVICE')),
      isPc: this.isPc(),
      isOpen: false,
      langMb: 1,
    }
  },
  created() {
  },
  methods: {
    switchLang(item) {
      this.$i18n.locale = item.value;
      this.lang = item.value;
    },
    enterOtherPage(path, name) {
      if (!this.$route.path.endsWith(path)) {
        if (path === 'user/service') {
          if (!this.$route.path.endsWith('user/login')) {
            if (this.isLogin) {
              if (!this.$route.path.includes(path) || (this.$route.path.includes(path) && this.$route.path.includes('invoice'))) {
                this.$router.push({
                  path: `/user/service`,
                  name: name
                });
              }
            } else {
              this.$router.push({
                path: 'user/login',
                name: 'login'
              });
            }
          }
        } else {
          this.$router.push({
            path: path,
            name: name
          });
        }
      }
      if (!this.isPc) {
        this.isOpen = false;
      }
    },
    goToChildPage(item) {
      console.log(item)
      if (item.path === 'logout') {
        localStorage.clear();
        this.isLogin = false;
        this.serviceChildList = [];
        this.$router.push({
          path: 'user/login',
          name: 'login'
        });
      } else {
        if (!this.$route.path.endsWith(item.path)) {
          this.$router.push({path: item.path});
        }
      }
      if (!this.isPc) {
        this.isOpen = false;
      }
    },
    enterHomePage() {
      if (this.isPc || (!this.isPc && !(this.router.includes('order/') || this.router.includes('user/invoice')))) {
        if (!(this.$route.name === 'home')) {
          this.$router.push({
            path: '',
            name: 'home',
          });
        }
      } else if (!this.isPc && (this.router.includes('order/') || this.router.includes('user/invoice'))) {
        this.$router.back(-1);
      }
      if (!this.isPc) {
        this.isOpen = false;
      }
    },
    openMenu() {
      if (this.isOpen) {
        const that = this;
        TweenMax.fromTo(
            document.getElementById('menu-content'),
            0.3,
            {right: '0'},
            {
              css: {right: '-100%'}, ease: Power0.easeNone, onComplete: function () {
                that.isOpen = false
              }
            }
        )
      } else {
        this.isOpen = true;
        TweenMax.fromTo(
            document.getElementById('menu-content'),
            0.3,
            {right: '-100%'},
            {right: '0', ease: Power0.easeNone}
        )
      }

    }
  }
}
</script>

<style scoped src="../assets/css/hamburgers.css"></style>
<style lang="scss">
.logout {
  height: 30px;
  width: 100px;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
  margin: 0px;
  border: 0px solid #E4EAF0;

  &:hover {
    color: #3963EF;
    border-color: #3963EF;
  }
}

.lang-item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  width: 150px;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
  background-color: #FFFFFF;
  margin: 0px  -16px;
  color: #484652;

  &:hover {
    background-color: rgba(242, 246, 250, 0.6);
  }
}

.lang-item:first-child {
  margin-top: -12px;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}

.lang-item:last-child {
  margin-bottom: -15px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}

.checked-lang-item {
  background-color: rgba(242, 246, 250, 0.6);
}

.header-container {
  background: #fff;
  width: 100%;
  border-bottom: 1px solid #E5E9ED;

  .header-content {
    width: 1080px;
    height: 88px;
    margin: 0px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .header-left {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;

      i {
        width: 136px;
        height: 36px;
        background: center no-repeat;
        background-size: contain;
        background: url('../assets/logo-index.png') center no-repeat;
        margin-right: 14px;
      }

      span {
        font-size: 24px;
        color: #3963EF;
        font-weight: 600;
      }
    }

    .show-pointer {
      cursor: pointer;
    }

    .header-right {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;

      span {
        font-size: 16px;
        color: #242C45;
      }

      .item-help {
        margin-right: 52px;
        cursor: pointer;

        &:hover {
          color: #3963EF;
        }
      }

      .checked-router {
        color: #3963EF;
      }

      .item-product {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 112px;
        height: 44px;
        border: 1px solid #3963EF;
        color: #3963EF;
        border-radius: 4px;
        margin-right: 52px;
        cursor: pointer;
        background-color: #fff;

        &:hover {
          background-color: #3963EF;
          color: #fff;
        }
      }

      .item-service {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        cursor: pointer;

        span {
          margin-right: 6px;
          width: fit-content;
          text-align: right;
        }

        i {
          width: 8px;
          height: 5px;
        }

        .down-i {
          width: 8px;
          height: 5px;
          background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-lang-down.svg') center no-repeat;
          transition: transform 0.2s linear;
        }

        &:hover {
          .down-i {
            transform: rotate(180deg);
          }
        }
      }
    }
  }

  .header-content-mobile {
    width: 100%;

    .header-menu {
      width: 100%;
      height: 1.173rem;
      margin: 0px auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 0.533rem;
      border-bottom: 0.013rem solid #E5E9ED;

      .a-logo {
        width: 2.253rem;
        height: 0.587rem;
        background: url('../assets/logo-index.png') center no-repeat;
        background-size: contain;
      }

      .a-back {
        width: 0.493rem;
        height: 0.493rem;
        background: url('../assets/header/ic-header-back.svg') center no-repeat;
        background-size: contain;
      }

      .hamburger {
        padding: 0;

        &:focus {
          outline: none;
        }
      }
    }

    .menu-content {
      position: absolute;
      width: 100%;
      height: 100%;
      background: #fff;
      display: flex;
      flex-direction: column;
      right: -100%;
      z-index: 100;
      //top: 2rem;
      padding: 0.533rem 0 0 1.333rem;

      .header-link {
        margin: 0;
        display: flex;
        align-items: center;
        font-size: 0.427rem;
        font-weight: 400;
        height: 1.56rem;
        text-align: left;
        color: #242C45;
        border-bottom: 0.013rem solid #E5E9ED;
      }

      a, .subscribe-btn {
        margin: 0 0.533rem;
        font-size: 0.427rem;
        font-weight: 400;
      }

      .subscribe-btn {
        margin: 0.48rem 0 0 0;
        height: 0.96rem;
        width: 2.773rem;
        color: #3963EF;
        border-color: #3963EF;

        &:hover {
          color: #fff;
          background-color: #3963EF !important;
        }
      }

      .a-top-line {
        margin-top: 0.48rem;
        border-top: 0.027rem solid #E5E9ED;
      }

      .lang-switch {
        //margin-top: 0.48rem;
        height: 1.56rem;
        display: flex;
        flex-direction: row;
        //border-top: 0.027rem solid #E5E9ED;
        border-bottom: 0.027rem solid #E5E9ED;
        align-items: center;

        span {
          color: #3963EF;
        }

        .ant-radio-wrapper {
          .ant-radio {
            .ant-radio-inner {
              border-color: #3963EF;

              &:after {
                border: 1px solid #3963EF
              }
            }

          }
        }
      }
    }
  }
}
</style>
